    align-items: center;
    margin: 20px auto;
    width: 100%;
    max-width: 600px;
    border: 0px;
    border-radius: 8px;
  }
  
  /* ë©”ì¸ ì´ë¯¸ì§€ ë·° */
  .mainView {
