    overflow-x: auto; /* ê°€ë¡œ ìŠ¤í¬ë¡¤ í™œì„±í™” */
    gap: 15px;
    padding: 15px 10px 15px 10px;
    width: 100%; /* ì¸ë„¤ì¼ ë¦¬ìŠ¤íŠ¸ê°€ ê°¤ëŸ¬ë¦¬ ë„ˆë¹„ë¥¼ ë”°ë¥´ê²Œ ì„¤ì • */
    border-radius: 8px;
