  }
  
  /* ìŠ¤í¬ë¡¤ë°” ìŠ¤íƒ€ì¼ (ë¸Œë¼ìš°ì €ë³„ ì§€ì› ê°€ëŠ¥ì„± ìˆìŒ) */
  .thumbnailContainer::-webkit-scrollbar {
