  }
  
  .thumbnailContainer::-webkit-scrollbar-thumb {
    background-color: rgba(255, 107, 138, 0.2); /* ìŠ¤í¬ë¡¤ë°” ìƒ‰ìƒ */
    border-radius: 4px;
  }
  
