  }
  
  /* ì¸ë„¤ì¼ ë²„íŠ¼ */
  .thumbnailButton {
    border: none;
    background: none;
    padding: 0;
    cursor: pointer;
    border-radius: 5px;
    transition: transform 0.2s ease;
  }
  
  .thumbnailButton:hover {
    transform: scale(1.1);
  }
  
  .thumbnailImage {
    width: 80px;
    height: 80px;
    object-fit: cover;
    border: 2px solid transparent;
    border-radius: 5px;
    transition: transform 0.2s ease-in-out; /* ë¶€ë“œëŸ½ê²Œ ì „í™˜ */
  }
  
  .thumbnailButton.active .thumbnailImage {
    border-color: #007b5e; /* ì„ íƒëœ ì´ë¯¸ì§€ ê°•ì¡° ìƒ‰ìƒ */
    transform: scale(1.2);
  }
  
  .modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
  }
  
  .modalImage {
    max-width: 90%;
    max-height: 80%;
    border-radius: 8px;
    object-fit: contain;
  }
  
  /* ëª¨ë‹¬ ë„¤ë¹„ê²Œì´ì…˜ ë²„íŠ¼ ê³µí†µ ìŠ¤íƒ€ì¼ */
