.closeButton {
  position: absolute;
  top: 30px;
  right: 5px;
  transform: translateY(-50%);
  background: rgba(0, 0, 0, 0.3);
  border: none;
  color: white;
  cursor: pointer;
  z-index: 1001;
  padding: 0; 
  width: 50px; 
  height: 50px; 
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.closeButton:hover {
