.mainImageContainer {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  filter: brightness(1.1);
}

/* ë°°ê²½ ì´ë¯¸ì§€ */
.mainImage {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 100%;
  height: 100%;
  object-fit: contain; /* ğŸ’¡ ë¹„ìœ¨ ìœ ì§€í•˜ë©° ë””ë°”ì´ìŠ¤ ì‚¬ì´ì¦ˆì— ë§ì¶¤ */
  z-index: -1;
  transition: opacity 0.5s ease-in-out;
}


/* í˜ì´ë“œ íš¨ê³¼ */
.fadeIn {
  opacity: 1;
}

.fadeOut {
  opacity: 0;
}

/* í…ìŠ¤íŠ¸ ì˜¤ë²„ë ˆì´ */
.textOverlayContainer {
  position: absolute;
  bottom: 0;
  width: 100%;
  background-color: rgba(255, 255, 255, 0.7);
  padding: 20px 40px;
  text-align: center;
  box-shadow: 0px -4px 10px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.mainText {
  position: absolute;
  top: 15%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 1.5rem;
  color: #000;
  font-family: 'Malgun Gothic', sans-serif;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.3);
  padding: 3px 8px;
  border-radius: 8px;
  text-align: center;
  line-height: 1.6;
  letter-spacing: 1px;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.3);
  white-space: pre-line;
  max-width: 80%;
}

.coupleNames {
  font-size: 2rem;
  font-weight: normal;
  color: #333;
  font-family: 'Malgun Gothic', sans-serif;
  margin: 0;
  text-align: center;
  letter-spacing: 8px;
}

.detailsText {
  font-size: 1rem;
  color: #333;
  font-family: 'Malgun Gothic', sans-serif;
  margin-top: 20px;
  margin-bottom: 0;
  line-height: 1.6;
  text-align: center;
  white-space: pre-line;
}

@media (max-width: 480px) {
  .mainText {
    font-size: 1.3rem;
  }

  .coupleNames {
    font-size: 1.8rem;
  }

  .detailsText {
    font-size: 0.9rem;
  }

  .textOverlayContainer {
    padding: 15px 30px;
  }
}
